<script>
	import SelectorTreeLeaf from './SelectorTreeLeaf.svelte';
	export let expanded = false;
	export let label, items

	function toggle() { expanded = !expanded }
</script>

<span class:expanded on:click={toggle}>{label}</span>

{#if expanded}
	<ul>
		{#each items as item}
			<li>
				{#if item.type === 'container'}
					<svelte:self {...item}/>
				{:else}
					<SelectorTreeLeaf {...item}/>
				{/if}
			</li>
		{/each}
	</ul>
{/if}

<style>
	span {
		padding: 0 0 0 1.5em;
		background: url(https://svelte.dev/tutorial/icons/folder.svg) 0 0.1em no-repeat;
		background-size: 1em 1em;
		font-weight: bold;
		cursor: pointer;
	}

	.expanded {
		background-image: url(https://svelte.dev/tutorial/icons/folder-open.svg);
	}

	ul {
		padding: 0.2em 0 0 0.5em;
		margin: 0 0 0 0.5em;
		list-style: none;
		border-left: 1px solid #eee;
	}

	li {
		padding: 0.2em 0;
	}
</style>
